<template>
    <section id="button-section" class="section" @click="click()">
        <div id="button1" class="button">
            <span class="text">Click Here</span>
            <span class="border top"></span>
            <span class="border right"></span>
            <span class="border bottom"></span>
            <span class="border left"></span>
        </div>
    </section>
</template>

<script>
    export default {
        name: "IntroductionButton",
        methods: {
            click() {
                this.$emit('clicking')
            }
        }
    }
</script>

<style scoped lang="scss">
    $orange: #e94f08;

    *, *::before, *::after{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
    }

    .section{
        float: left;
        font-family: 'Slabo 27px', serif;
        height: 50%;
        width: 50%;
        display: flex;
        -webkit-display: flex;
        -webkit-align-items: center;
        align-items: center;
        justify-content: center;
        -webkit-justify-content: center;
    }

    .button{
        border: none;
        cursor: pointer;
        padding: 35px 70px;
        position: relative;
        .text{
            position: relative;
            z-index: 100;
            font-size: 2em;
        }
    }

    #button-section{
        background-color: black;
        width: 100%;
        margin-right: auto;
        margin-left: auto;
        margin-top: 10rem;
                #button1{
                    background-color: black;
                .text{
                    color: $orange;
                    -webkit-transition: color .3s linear;
                    transition: color .3s linear;
                }
                .border{
                    transition: all .3s ease-out;
                    -webkit-transition: all .3s ease-out;
                    background-color: $orange;
                    position: absolute;
                }
                .top,.left{
                @extend .border;
                    left: 0;
                    top: 0;
                }
                .bottom,.right{
                @extend .border;
                    right: 0;
                    bottom: 0;
                }
                .right,.left{
                    width: 2px;
                    height: 0%;
                }
                .bottom,.top{
                    height: 2px;
                    width: 0%;
                }
                &::after{
                     content: '';
                     transition: all .3s ease-out;
                     -webkit-transition: all .3s ease-out;
                     position: absolute;
                     left: 50%;
                     top: 50%;
                     opacity: 0;
                     transform: translate(-50%, -50%);
                     width: 0px;
                     height: 0px;
                     background-color: $orange;
                 }
                &:hover{
                .text{ color: black; }
                > .bottom,.top{
                    width: 100%;
                }
                > .right,.left{
                    height: 100%;
                }
                &::after{
                     width: 100%;
                     height: 100%;
                     opacity: .5;
                 }
            }
        }
    }
</style>



