<template>
    <div class="index-area">
        <div class="tile is-ancestor">
            <div class="tile is-parent">
                <div class="tile is-3 is-child"></div>
                    <div class="tile is-6 is-child text-wrapper">
                        <transition name="welcome" appear>
                            <p>Welcome!</p>
                        </transition>
                        <transition name="text1" appear>
                            <p>This is Kenta Takimura's profile site</p>
                        </transition>
                    </div>
                <div class="tile is-3 is-child"></div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Top"
    }
</script>

<style scoped>
    p {
        margin-top: 5rem;
        width: 100%;
        color: #00ff00;
        font-size: 2em;
        text-align: center;
    }
    .welcome-enter-active {
        transition: opacity 1.5s;
    }
    .welcome-enter {
        opacity: 0;
    }
    .text1-enter-active {
        transition: opacity 1.5s;
        transition-delay: 1.5s;
    }
    .text1-enter {
        opacity: 0;
    }
</style>



