import Index from './pages/Index'
import Top from './pages/Top'
import Me from './pages/Me'
import Skills from './pages/Skills'
import Outputs from './pages/Outputs'
import Contacts from './pages/Contacts'

export default {
    routes: [
        {
            path: '/',
            component: Index,
            name: 'index'
        },
        {
            path: '/top',
            component: Top,
            name: 'top'
        },
        {
            path: '/me',
            component: Me,
            name: 'me'
        },
        {
            path: '/skills',
            component: Skills,
            name: 'skills'
        },
        {
            path: '/outputs',
            component: Outputs,
            name: 'outputs'
        },
        {
            path: '/contacts',
            component: Contacts,
            name: 'contacts'
        }
    ]
}



