<template>
    <header id="header">
        <nav class="navbar is-success" role="navigation" aria-label="main navigation">
            <div class="navbar-brand">
                <a role="button"
                   class="navbar-burger burger has-dropdown"
                   aria-label="menu"
                   aria-expanded="false"
                   data-target="navbarMenu"
                   :class="{'is-active': isActive}"
                   @click="toggleIsActive">
                    <span aria-hidden="true"></span>
                    <span aria-hidden="true"></span>
                    <span aria-hidden="true"></span>
                </a>
            </div>
            <div id="navbarMenu"
                 class="navbar-menu"
                 :class="{'is-active': isActive}">
                <div class="navbar-start">
                    <router-link :to="{name: 'index'}" exact class="navbar-item">Top</router-link>
                    <router-link :to="{name: 'me'}" exact class="navbar-item">Me</router-link>
                    <router-link :to="{name: 'skills'}" exact class="navbar-item">Skills</router-link>
                    <router-link :to="{name: 'outputs'}" exact class="navbar-item">Outputs</router-link>
                    <router-link :to="{name: 'contacts'}" exact class="navbar-item">Contacts</router-link>
                </div>
            </div>
        </nav>
    </header>
</template>

<script>
    import BTabs from "buefy/src/components/tabs/Tabs";
    import BTabItem from "buefy/src/components/tabs/TabItem";
    export default {
        name: "Header",
        components: {BTabItem, BTabs},
        data: function () {
            return {
                isActive: false
            }
        },
        methods: {
            toggleIsActive() {
                this.isActive = !this.isActive
            }
        }
    }
</script>

<style scoped>
    .navbar-start {
        margin-left: auto;
        margin-right: auto;
    }
</style>



