<template>
    <div class="index-area">
        <div class="tile is-ancestor">
            <div class="tile is-parent">
                <div class="tile is-3 is-child"></div>
                <vue-typer
                    text='Class KentaTakimuraProfileSite {
              public static void main (String args[ ]) {
                        start();
              }
     }'
                :repeat="0"
                :preTypeDelay="1000"
                class="tile is-6 is-child"
                @completed="onComplete"
                v-if="showText"></vue-typer>
                <div class="tile is-6 is-child" v-if="showBtn">
                    <IntroductionButton @clicking="click"></IntroductionButton>
                </div>
                <div class="tile is-3 is-child"></div>
            </div>
        </div>
    </div>
</template>

<script>
    import { VueTyper } from 'vue-typer'
    import IntroductionButton from "../components/IntroductionButton";

    export default {
        name: "Index",
        components: {
            IntroductionButton,
            VueTyper
        },
        data: function () {
            return {
                showText: true,
                showBtn: false
            }
        },
        methods: {
            onComplete: function () {
                setTimeout(this.hideCode, 1000)
                setTimeout(this.showButton, 1000)
            },
            hideCode: function () {
                this.showText = false
            },
            showButton: function () {
                this.showBtn = true
            },
            click() {
                this.$router.push({name: 'top'})
            }
        }
    }
</script>

<style lang="scss">
    .index-area {
        width: 100%;
        padding: 20px 10px;
    }
    .vue-typer {
        .custom.char {
            &.typed {
                color: #00ff00;
                font-size: 1em;
            }
        }
    }
</style>



