<template>
    <div class="columns is-multiline">
        <div class="column is-one-third" v-for="(output, key) of outputs">
            <a :href="output.url" target="_blank" class="card box">
                <div class="card-image">
                    <figure class="image is-4by3">
                        <img :src="output.img" :alt="output.alt"/>
                    </figure>
                </div>
                <div class="card-content">
                    <div class="media">
                        <div class="media-content">
                            <p class="title is-4">{{ output.title }}</p>
                        </div>
                    </div>
                    <div class="content" v-html="output.text"></div>
                </div>
            </a>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Card",
        props: {
            outputs: Array
        }
    }
</script>

<style scoped>
    a:hover {
        border: 2px solid #FF0000;
        box-shadow: 5px 5px rgba(169, 169, 169, 0.8);
    }
</style>



