<template>
    <div>
        <div class="tile is-ancestor" v-for="(article, index) of data" :key="index">
            <div class="tile is-parent is-info">
                <div class="tile is-3 is-child"></div>
                <div class="tile is-6 is-child" >
                    <div class="title">{{ article.title }}</div>
                    <article class="box">
                        <div v-for="(item, index) of article.contents" v-if="article.options === 'WITH_LABEL'" :key="index" class="me_inline_item">
                            <div class="me_inline_label">
                                <span>{{ item.label }} : </span>
                            </div>
                            <div class="me_inline_value" v-if="item.type === 'text'">
                                <span>{{ item.value }}</span>
                            </div>
                            <div class="me_inline_value" v-if="item.type === 'url'">
                                <span><a :href="item.url" target="_blank">{{ item.value }}</a></span>
                            </div>
                        </div>
                        <div v-for="(item, index) of article.contents" v-if="article.options === 'WITHOUT_LABEL'" :key="index" class="me_inline_item">
                            {{ item.value }}
                        </div>
                    </article>
                </div>
                <div class="tile is-3 is-child"></div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Tile",
        props: {
            data: Array
        }
    }
</script>

<style scoped>
    .title {
        color: #FFFFFF;
        font-size: 1.5em;
    }
    .is-ancestor {
        margin: 10px 0 0;
        width: 100%;
    }
    .is-child {
        margin: auto;
    }
    a {
        text-decoration: none;
        color: #00FA9A;
    }
    .me_inline_item {
        width: 100%;
        height: 34px;
        clear: both;
        margin-top: 16px;
        margin-bottom: 16px;
    }
    .me_inline_label {
        float: left;
        width: 20%;
        text-align: right;
        margin-top: 5px;
        margin-bottom: 5px;
        word-wrap: break-word;
    }
    .me_inline_value {
        float: right;
        width: 80%;
        margin-top: 5px;
        margin-bottom: 5px;
        padding-left: 8px;
        word-wrap: break-word;
    }
    @media (max-width: 972px) {
        .me_inline_item {
            clear: none;
            height: auto;
            margin-top: 16px;
            margin-bottom: 16px;
        }
        .me_inline_label {
            float: none;
            width: 100%;
            text-align: left;
        }
        .me_inline_value {
            width: 100%;
            float: none;
            text-align: left;
        }
    }
</style>



