    import Header from './components/Header'
    export default {
        name: 'App',
        components: {
            Header
        }
    }
</script>

<style>
    #app {
      font-family: 'Avenir', Helvetica, Arial, sans-serif;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      color: #2c3e50;
    }
    html, body {
        height: 100%;
    }
    html, body {
        background-color: rgba(0, 0, 0, 1);
    }
</style>



