<template>
    <transition>
        <div id="outputs">
            <Card :outputs="outputs"></Card>
        </div>
    </transition>
</template>

<script>
    import Card from "../components/Card";
    export default {
        name: "Outputs",
        components: {Card},
        data: function () {
            return {
                outputs: [
                    {
                        title: 'Chat-Space',
                        text: 'プログラミングスクールで作成したwebチャットアプリです。' +
                        '<br>' +
                        '<br>使用言語' +
                        '<br>Ruby(Ruby on Rails)' +
                        '<br>HTML(Haml)' +
                        '<br>CSS(SCSS)',
                        img: require('../assets/outputs/chat-space.png'),
                        alt: 'chat-space',
                        url: 'https://github.com/kennyTakimura/chat-space'
                    },
                    {
                        title: 'Profile Site',
                        text: '本プロフィールサイトです。' +
                        '<br>' +
                        '<br>使用言語' +
                        '<br>Vue.js' +
                        '<br>HTML' +
                        '<br>CSS(Buefy(Bulma))',
                        img: require('../assets/outputs/profile-site.png'),
                        alt: 'profile-site',
                        url: 'https://github.com/kennyTakimura/kennyTakimura-profile'
                    },
                ]
            }
        }
    }
</script>

<style scoped>
    #outputs {
        padding: 20px 10px;
        background-color: #000000;
    }
    .v-enter-active {
             transition: .5s;
         }
    .v-enter {
        transform: translate(0, -100%);
    }
    .v-leave-to {
        transform: translate(0, 100%);
    }
</style>



