<template>
    <div id="skills">
        <section>
            <b-tabs size="is-medium" class="block" position="is-centered">
                <b-tab-item label="Languages">
                    <transition>
                        <VueAccordion
                            :items="languageData"></VueAccordion>
                    </transition>
                </b-tab-item>
                <b-tab-item label="Tools">
                    <VueAccordion
                        :items="toolsData"></VueAccordion>
                </b-tab-item>
            </b-tabs>
        </section>
    </div>
</template>

<script>
    import BTabs from "buefy/src/components/tabs/Tabs";
    import BTabItem from "buefy/src/components/tabs/TabItem";
    import BCollapse from "buefy/src/components/collapse/Collapse";
    import VueAccordion from "vue-faq-accordion";

    export default {
        name: "Skills",
        components: {
            BCollapse,
            BTabItem,
            BTabs,
            VueAccordion
        },
        data: function () {
            return {
                languageData: [
                    {
                        title: 'Java',
                        value: '最も利用歴の長い言語です。' +
                        '<br>Framework: Spring' +
                        '<br>経験: 2年'
                    },
                    {
                        title: 'Ruby',
                        value: 'プログラミングスクールで学んだことを期に利用し始めました。' +
                        '<br>Javaの次に利用しているサーバサイド言語です。' +
                        '<br>Framework: Ruby on Rails' +
                        '<br>経験: 1年'
                    },
                    {
                        title: 'JavaScript',
                        value: 'Angular.jsについては、TypeScriptでの実装経験もあります。' +
                        '<br>Framework: Vue.js, Angular.js' +
                        '<br>経験(Vue.js): 1年' +
                        '<br>経験(Angular.js): 1年'
                    },
                    {
                        title: 'HTML',
                        value: 'Hamlの利用経験もあります。' +
                        '<br>経験: 1年'
                    },
                    {
                        title: 'CSS',
                        value: 'SCSSの利用経験もあります。' +
                        '<br>経験: 1年'
                    }
                ],
                toolsData: [
                    {
                        title: 'AWS',
                        value: 'EC2、VPC、RDSなどの基本的なツールを利用しています。' +
                        '<br>経験: 1年'
                    },
                    {
                        title: 'Docker',
                        value: '最近学習をはじめました。Udemyで学習させてもらっています。' +
                        '<br><a href="https://www.udemy.com/docker-mastery/" target="_blank">Docker Mastery: The Complete Toolset From a Docker Captain</a>' +
                        '<br>経験: 学習中...'
                    },
                    {
                        title: '英語',
                        value: '日本語の次によく使っています。' +
                        '<br>業務で利用することもあり、コミュニケーションはもちろん支障ありません。'
                    }
                ]
            }
        }
    }
</script>

<style>
    #skills {
        padding: 20px 10px;
        background-color: #000000;
    }
    .tabs ul {
        border-bottom-color: #A9A9A9;
    }
    .tabs li a {
        color: #FFFFFF;
        border-bottom-color: #A9A9A9;
    }
    .tabs li a:hover {
         color: #A9A9A9 !important;
         border-bottom-color: #A9A9A9 !important;
     }
    .tabs li.is-active a {
        border-bottom-color: #FF0000 !important;
        color: #FF0000 !important;
    }
    .tabs {
        background-color: rgba(16, 16, 16, 0.82);
    }
    .accordion {
        background-color: white;
    }
    .v-enter-active {
        transition: .5s;
    }
    .v-enter {
        transform: translate(100%, 0);
    }
    .v-leave-to {
        transform: translate(-100%, 0);
    }
</style>



